.meter {
  height: 1.3rem;
  width: 50vw;
  position: relative;
  background: transparent;
  border-radius: 30px;
  padding: 6px;
  box-shadow: inset 0 -1px 1px rgba(255, 255, 255, 0.3);
}
.meter > span {
  display: block;
  height: 80%;
  border: 20px 8px 8px 20px;
  border-radius: 70px;
  background-color: rgb(102, 38, 204);
  position: relative;
  text-align: center;
  font-size: 1rem;
  z-index: 5;
}
